@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
*{padding:0;margin:0;box-sizing:border-box;font-family:"Kanit",sans-serif}*:not(input){color:#fff}body{background-color:#212121}form{display:flex;justify-content:center;align-items:center;flex-direction:column;height:50vh;background:#2980b9;margin:100px 450px;margin-bottom:60px}input,label{font-size:16px}input{outline:none;border:0;padding:10px;margin-left:5px}input[type=password]{margin-top:5px}input[type=email]{margin-left:31.5px}button{margin-top:20px;width:56%;line-height:2.5;font-size:17px;cursor:pointer;background-color:transparent;border:1px solid #fff;transition:all .3s}button:hover{background:#fff;color:#2980b9;transition:all .3s}div.result{-webkit-transform:translate(40%, 0);transform:translate(40%, 0)}div.result code{color:#39ff14}
